export default {
    name: 'order-success',
    data () {
        return {
            orders: {},
            order_id : '',
            openid : '',
            tel:'',
        }
    },
    created () {
        this.order_id = this.$route.query.order_id;
        this.getOrderList()
    },
    methods: {
        myOrderListPath () {
            this.$router.push({path: '/order/mylist'})
        },
        homePath () {
            tmp_room_id = localStorage.getItem('roomid')
            this.$router.push({path: '/room', query:{room_id:tmp_room_id}})
        },
        getOrderList () {
            var that = this;
            if(!that.order_id){
                this.$vux.alert.show({
                    title: '温馨提示',
                    content: "无效订单号"});
                return false;
            }


            var formdata = new FormData();
            formdata.append('order_id', that.order_id);

            that.axiosPost('/client/orders', formdata).then((res) => {
                that.orders = res.data[0]
                that.tel = that.orders.mobile ? "tel:"+that.orders.mobile : "";
            }, (err) => {
                console.log(err)
            })
        }
    }
}



