export default {
    name: 'index',
    props: ['room_id'],
    data () {
        return {
            roomList: {},
            order_id : '',
            openid : '',
        }
    },
    created () {
        this.getRoomList()
    },
    methods: {
        roomPath (id) {
            this.$router.push({path: '/room', query:{room_id:id}})
        },
      getRoomList () {
            let that = this;
            var formdata = new FormData();
            // formdata.append('order_id', that.order_id);
            // formdata.append('open_id', localStorage.getItem('openid'));
            that.axiosPost('/room/list', formdata).then((res) => {
                that.roomList = res.data
                console.log(that.roomList)
            }, (err) => {
                console.log(err)
            })
        }
    }
}



